es/modules/bootstrap-overrides/overrides';\n\n/**\n* IMPORTING PAGES MODULES\n*/\n@import 'styles/pages/user-actions/form-pages';\n\n/**\n* IMPORTING MAIL COMPOSER MODULES\n*/\n@import 'styles/modules/mail-composer/mail-composer';\n\n// Components global styles\n@import 'styles/components.styles';\n\n.loading-icon {\n  position: absolute;\n  top: 28vh;\n  margin-left: calc(50% - 20px);\n}\n\n// LOADING\n\n@keyframes pulse_animation {\n  0% {\n    transform: scale(1);\n  }\n  30% {\n    transform: scale(1);\n  }\n  40% {\n    transform: scale(1.15);\n  }\n  50% {\n    transform: scale(1);\n  }\n  60% {\n    transform: scale(1);\n  }\n  70% {\n    transform: scale(1.08);\n  }\n  80% {\n    transform: scale(1);\n  }\n  100% {\n    transform: scale(1);\n  }\n}\n\n.loading {\n  padding: 0;\n  margin: 0;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background: #3a4e63;\n  width: 100%;\n  height: 100%;\n  text-align: center;\n}\n\n.quote {\n  margin: 0;\n  color: #ffffff;\n  font-size: 24px;\n  max-width: 80vw;\n}\n\n.author {\n  font-size: 18px;\n  color: #ffffff;\n}\n\n.cdk-overlay-container {\n  position: fixed;\n  bottom: 0px;\n  width: 100%;\n}\n\n.cursor-pointer {\n  cursor: pointer;\n}\n\n.ql-font-lato {\n  font-family: 'Lato', sans-serif;\n}\n\n.ql-font-roboto {\n  font-family: Roboto, sans-serif;\n}\n\n.ql-font-arial {\n  font-family: Arial, sans-serif;\n}\n\n.ql-font-times-new-roman {\n  font-family: Times New Roman, serif;\n}\n\n.generic-dropdown-list {\n  max-height: 300px;\n  overflow-y: scroll;\n}\n\n//START tag input css\n\ntag-input {\n  .ng2-tag-input {\n    border-bottom: 0 !important;\n\n    // TODO @kriti please fix this width for multiple devices.\n    //width: 650px;\n  }\n\n  .ng2-tag-input__text-input {\n    height: 20px !important;\n    font-size: 14px !important;\n  }\n\n  tag {\n    height: 20px !important;\n    border-radius: 20px !important;\n    border: 1px solid lightgrey !important;\n\n    .tag-wrapper {\n      margin-top: -10px !important;\n    }\n  }\n\n  .ng2-dropdown-menu {\n    margin-top: -15px;\n  }\n}\n\n.btn-user-action {\n  border-radius: 2px;\n  cursor: pointer;\n  font-size: 11px;\n  text-align: center;\n  margin-right: 10px;\n  line-height: 2rem;\n  min-width: 54px;\n  outline: 0;\n  padding: 0 8px;\n  background-color: #f1f1f1;\n  border: 1px solid rgba(0, 0, 0, 0.1);\n  color: #444;\n\n  .icon-lg {\n    font-size: 1.1rem;\n    vertical-align: middle;\n  }\n\n  &:hover,\n  &:focus {\n    border: 1px solid #c6c6c6;\n    color: #333;\n  }\n\n  &:active {\n    background: #dedede;\n  }\n}\n\n.position-left {\n  margin-right: 7px;\n}\n\n.btn {\n  cursor: pointer;\n}\n//END tag input css\n\n.zEWidget-launcher {\n  z-index: 10 !important;\n}\n\n.green {\n  color: green;\n}\n\n.close-mat-keyboard {\n  position: absolute;\n  top: 5px;\n  right: 5px;\n  border-radius: 50%;\n  border: 1px solid #333;\n  background: #333;\n  color: white;\n  height: 20px;\n  width: 20px;\n  text-align: center;\n  opacity: 1;\n\n  &:hover {\n    color: white;\n    opacity: 0.8;\n  }\n\n  .close-mat-keyboard-icon {\n    font-size: 12px;\n    vertical-align: middle;\n  }\n}\n\napp-compose-mail {\n  .mail-compose-field tag-input {\n    max-height: 75px;\n    overflow-y: auto;\n    max-width: calc(100% - 4rem);\n    min-width: calc(100% - 4rem);\n  }\n\n  .mail-compose-field textarea {\n    font-size: 14px;\n  }\n}\n\nbutton[disabled] {\n  cursor: not-allowed;\n  background-color: #f1f0f0;\n}\n\n.menu-open .mailbox-sidebar {\n  @include media('<sm') {\n    left: 220px;\n  }\n}\n\n.btn-social {\n  color: #fff;\n  font-size: 14px;\n  &:hover,\n  &:focus {\n    color: #fff;\n  }\n\n  img {\n    max-height: 15px;\n  }\n}\n\n.btn-twitter {\n  background-color: $twitter-color;\n  border-color: $twitter-color;\n\n  &:hover,\n  &:focus {\n    background-color: darken($twitter-color, 10%);\n    border-color: darken($twitter-color, 10%);\n  }\n}\n\n.btn-facebook {\n  background-color: $facebook-color;\n  border-color: $facebook-color;\n  &:hover,\n  &:focus {\n    background-color: darken($facebook-color, 10%);\n    border-color: darken($facebook-color, 10%);\n  }\n}\n\n// Settings Tabs\n\n.nav-tabs {\n  box-shadow: 0 4px 10px 0 rgba(1, 70, 109, 0.1);\n}\n\n.nav-tabs.nav {\n  display: flex;\n  padding-left: 0;\n  margin-bottom: 0;\n  list-style: none;\n  border-bottom: 1px solid #f5f5f5;\n}\n\n.nav-tabs.nav li {\n  border-right: 1px solid #f5f5f5;\n}\n\n.nav-tabs.nav .nav-link {\n  display: flex;\n  align-items: center;\n  height: 100%;\n  text-decoration: none;\n  background-color: transparent;\n  padding: 1rem 0.9rem;\n  text-transform: unset;\n  border-radius: 0.25rem;\n  font-size: 14px;\n  font-weight: 600;\n  color: #616161;\n  text-align: center;\n  max-width: 175px;\n}\n\n@media (max-width: 767px) {\n  .nav-tabs.nav {\n    flex-direction: column;\n  }\n\n  .nav-tabs.nav .nav-link {\n    text-align: left;\n    max-width: 100%;\n  }\n}\n\n.nav-tabs.nav {\n  .nav-link {\n    &:hover,\n    &.active {\n      background: #f4f4f4;\n      border-radius: 0;\n    }\n  }\n}\n\n.nav-tabs.nav .nav-link.active {\n  color: $brand-primary;\n}\n\n.gmail_extra,\n.gmail_quote,\n.ctemplar_quote {\n  display: none;\n}\n\n.show_gmail_extra .gmail_extra,\n.show_gmail_extra .gmail_quote {\n  display: block;\n}\n\n.l-alert-custom {\n  border: none !important;\n  background-color: #e3f1fd;\n  font-size: 14px;\n  position: relative;\n  z-index: 1;\n  i {\n    vertical-align: middle;\n  }\n}\n\n.text-grey-dark {\n  color: #727272;\n}\n\n.text-grey-dark-500 {\n  color: #757575;\n}\n\n.accordion .card {\n  margin-bottom: 10px;\n  border-radius: 0;\n  border-color: #f2f2f2;\n}\n\n.accordion .card {\n  .card-header {\n    border-bottom: 0;\n  }\n\n  .card-header:first-child {\n    padding: 0;\n    a {\n      color: rgba(0, 0, 0, 0.54);\n    }\n  }\n}\n\n.mail-checkbox {\n  mat-checkbox {\n    label {\n      margin-bottom: 0 !important;\n    }\n\n    .mat-checkbox-frame {\n      border: 2px solid #cccaca;\n    }\n  }\n}\n\n.show-ellipses {\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n.mat-simple-snackbar-action {\n  color: $brand-primary !important;\n}\n\n.hidden {\n  visibility: hidden;\n}\n\n.incoming-headers {\n  .col-3,\n  .col-9 {\n    border: 1px solid $brand-secondary;\n    border-bottom: 0;\n  }\n\n  .col-9 {\n    border-left: 0;\n    overflow-wrap: break-word;\n  }\n\n  .last-row {\n    .col-3,\n    .col-9 {\n      border-bottom: 1px solid $brand-secondary;\n    }\n  }\n}\n\n.download-attachment-wrapper {\n  display: inline-block;\n  border-radius: 2px;\n  background-color: #f5f5f5;\n  margin-bottom: 4px;\n\n  .download-attachment {\n    display: flex;\n    justify-content: flex-start;\n    align-items: center;\n    padding: 0.5rem;\n    border: 1px solid #e5e5e5;\n    color: #444;\n\n    &:hover {\n      border: 1px solid #4177f6;\n    }\n  }\n\n  .attachment-icon {\n    float: left;\n    height: 20px;\n    width: 20px;\n    text-align: center;\n    vertical-align: middle;\n    background: #e74b3e;\n    color: white;\n    border-radius: 2px;\n    font-size: 14px;\n    margin-right: 5px;\n  }\n\n  .attachment-name {\n    font-size: 12px;\n    font-weight: bold;\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    max-width: 200px;\n  }\n}\n\n.full-width {\n  width: 100% !important;\n}\n\n.position-relative {\n  position: relative;\n}\n\n.display-inline-flex {\n  display: inline-flex;\n}\n\n.input-ok,\n.input-invalid {\n  float: right;\n  margin-right: -28px;\n  margin-top: -31px;\n  color: green;\n}\n\n.input-invalid {\n  color: #dc3545 !important;\n}\n\n.font-20px {\n  font-size: 20px;\n}\n\n.learn-more-info-icon {\n  color: $gray-9e9e9e;\n  display: inline-block;\n  position: absolute;\n  margin-left: 8px;\n\n  i {\n    font-size: 17px;\n    margin-top: 2px;\n  }\n}\n\n.loading-spinner {\n  position: absolute;\n  right: 25px;\n  top: 25px;\n}\n\n.dropup .dropdown-menu {\n  bottom: unset !important;\n  transition: inherit;\n}\n","/**\n* master sass file, which includes all the partial files\n*/\n@import url(~@angular/material/prebuilt-themes/indigo-pink.css);\n/**\n* IMPORTING PARTIALS FROM CONFIG DIRECTORY\n*/\n/*\n** @partial : _include-media-config.scss, which includes configurations for viewport/media query related stuffs\n*/\n@media (min-width: 1000px) {\n  .hidden-md-up {\n    display: none !important;\n  }\n}\n\n.hidden-sm-down {\n  display: unset !important;\n}\n\n@media (max-width: 999px) {\n  .hidden-sm-down {\n    display: none !important;\n  }\n}\n\n/**\n* @partial : _variables.scss\n*/\n/** \n* ==============================================================================\n*     Defining custom variables to be used throughout the project\n* ==============================================================================\n*/\n/** \n* ==============================================================================\n*     Overriding bootstrap's varaibles with own brand's variables\n* ==============================================================================\n*/\n/**\n* IMPORTING PARTIALS FROM VENDORS DIRECTORY\n*/\n/*!\n * Font Awesome Free 5.0.9 by @fontawesome - https://fontawesome.com\n * License - https://fontawesome.com/license (Icons: CC BY 4.0, Fonts: SIL OFL 1.1, Code: MIT License)\n */\n.fa,\n.fas,\n.far,\n.fal,\n.fab {\n  -moz-osx-font-smoothing: grayscale;\n  -webkit-font-smoothing: antialiased;\n  display: inline-block;\n  font-style: normal;\n  font-variant: normal;\n  text-rendering: auto;\n  line-height: 1;\n}\n\n.fa-lg {\n  font-size: 1.33333333em;\n  line-height: 0.75em;\n  vertical-align: -0.0667em;\n}\n\n.fa-xs {\n  font-size: 0.75em;\n}\n\n.fa-sm {\n  font-size: 0.875em;\n}\n\n.fa-1x {\n  font-size: 1em;\n}\n\n.fa-2x {\n  font-size: 2em;\n}\n\n.fa-3x {\n  font-size: 3em;\n}\n\n.fa-4x {\n  font-size: 4em;\n}\n\n.fa-5x {\n  font-size: 5em;\n}\n\n.fa-6x {\n  font-size: 6em;\n}\n\n.fa-7x {\n  font-size: 7em;\n}\n\n.fa-8x {\n  font-size: 8em;\n}\n\n.fa-9x {\n  font-size: 9em;\n}\n\n.fa-10x {\n  font-size: 10em;\n}\n\n.fa-fw {\n  text-align: center;\n  width: 1.25em;\n}\n\n.fa-ul {\n  list-style-type: none;\n  margin-left: 2.5em;\n  padding-left: 0;\n}\n\n.fa-ul > li {\n  position: relative;\n}\n\n.fa-li {\n  left: -2em;\n  position: absolute;\n  text-align: center;\n  width: 2em;\n  line-height: inherit;\n}\n\n.fa-border {\n  border: solid 0.08em #eee;\n  border-radius: 0.1em;\n  padding: 0.2em 0.25em 0.15em;\n}\n\n.fa-pull-left {\n  float: left;\n}\n\n.fa-pull-right {\n  float: right;\n}\n\n.fa.fa-pull-left,\n.fas.fa-pull-left,\n.far.fa-pull-left,\n.fal.fa-pull-left,\n.fab.fa-pull-left {\n  margin-right: 0.3em;\n}\n\n.fa.fa-pull-right,\n.fas.fa-pull-right,\n.far.fa-pull-right,\n.fal.fa-pull-right,\n.fab.fa-pull-right {\n  margin-left: 0.3em;\n}\n\n.fa-spin {\n  animation: fa-spin 2s infinite linear;\n}\n\n.fa-pulse {\n  animation: fa-spin 1s infinite steps(8);\n}\n\n@keyframes fa-spin {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n.fa-rotate-90 {\n  -ms-filter: \"progid:DXImageTransform.Microsoft.BasicImage(rotation=1)\";\n  transform: rotate(90deg);\n}\n\n.fa-rotate-180 {\n  -ms-filter: \"progid:DXImageTransform.Microsoft.BasicImage(rotation=2)\";\n  transform: rotate(180deg);\n}\n\n.fa-rotate-270 {\n  -ms-filter: \"progid:DXImageTransform.Microsoft.BasicImage(rotation=3)\";\n  transform: rotate(270deg);\n}\n\n.fa-flip-horizontal {\n  -ms-filter: \"progid:DXImageTransform.Microsoft.BasicImage(rotation=0, mirror=1)\";\n  transform: scale(-1, 1);\n}\n\n.fa-flip-vertical {\n  -ms-filter: \"progid:DXImageTransform.Microsoft.BasicImage(rotation=2, mirror=1)\";\n  transform: scale(1, -1);\n}\n\n.fa-flip-horizontal.fa-flip-vertical {\n  -ms-filter: \"progid:DXImageTransform.Microsoft.BasicImage(rotation=2, mirror=1)\";\n  transform: scale(-1, -1);\n}\n\n:root .fa-rotate-90,\n:root .fa-rotate-180,\n:root .fa-rotate-270,\n:root .fa-flip-horizontal,\n:ro