a-expressions: $global-media-expressions !global;\n}\n\n@mixin media($conditions...) {\n  // scss-lint:disable SpaceAroundOperator\n  @if ($im-media-support and length($conditions) == 0) or\n    (not $im-media-support and im-intercepts-static-breakpoint($conditions...))\n  {\n    @content;\n  } @else if ($im-media-support and length($conditions) > 0) {\n    @media #{unquote(parse-expression(nth($conditions, 1)))} {\n      // Recursive call\n      @include media(slice($conditions, 2)...) {\n        @content;\n      }\n    }\n  }\n}\n","/**\n* Global scss file which includes some global style rules\n*/\n\n/* Web page customized selection feature */\n@include selection($brand-primary, $brand-white);\n\n// == Root element\nhtml {\n  margin: 0 0;\n  padding: 0 0;\n  font-size: 16px;\n  height: 100%;\n  -ms-text-size-adjust: 100%;\n  -webkit-text-size-adjust: 100%;\n  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);\n  -webkit-font-smoothing: antialiased;\n  width: 100%;\n}\n\nbody {\n  margin: 0 0;\n  padding: 0 0;\n  height: 100%;\n}\n\n// == app outer wrapper\n.app-outer {\n  overflow-x: hidden;\n  overflow-y: hidden;\n  position: relative;\n\n  // == inner page app outer\n  .inner-template &,\n  &.app-outer-secondary {\n    padding-top: 3.75rem;\n\n    @include media('>sm') {\n      padding-top: 4.375rem;\n    }\n\n    @include media('>=ipad') {\n      padding-top: 5rem;\n    }\n  }\n\n  &.app-outer-mailbox {\n    padding-top: 0 !important;\n  }\n\n  &.app-outer-secondary {\n    &::before {\n      position: absolute;\n      top: 0;\n      right: 0;\n      left: 0;\n      z-index: $zindex-neg;\n      content: '';\n      background: $brand-secondary;\n      height: 80px;\n    }\n  }\n}\n\n// == custom container\n.container-lg {\n  max-width: 1310px;\n  width: 100%;\n}\n\n.container-xs {\n  max-width: 775px;\n  width: 100%;\n  padding-top: 95px;\n}\n\n.container-sm {\n  max-width: 905px;\n  width: 100%;\n\n  [class*='container-'] {\n    padding-right: 0;\n    padding-left: