box-shadow: 0px 10px 40px 0px transparentize($brand-success, 0.9);\n}\n\n// == Disable transition in window resize\n.resize-active * {\n  transition: none !important;\n}\n\n// == border radius\n.br-4 {\n  border-radius: 4px;\n}\n\n// == No background\n.no-bg {\n  background: transparent !important;\n}\n\n// == Icon sizes\n.icon-xs {\n  font-size: 0.5rem;\n}\n\n.icon-sm {\n  font-size: 0.625rem;\n}\n\n.icon-md {\n  font-size: 1.25rem;\n}\n\n// == Disable page scrolling\n.no-scroll {\n  overflow: hidden;\n}\n\n// == Custom background colors\n.bg-transparent {\n  background: transparent !important;\n}\n\n// == Scrollbar styling\n.has-custom-scroll::-webkit-scrollbar {\n  background: $brand-white;\n  width: 4px;\n  height: 10px;\n\n  @include media('>sm') {\n    width: 10px;\n    height: 10px;\n  }\n}\n\n.has-custom-scroll-sm::-webkit-scrollbar {\n  width: 4px;\n  height: 10px;\n}\n\n.has-custom-scroll::-webkit-scrollbar-button {\n  width: 0;\n  height: 0;\n  display: none;\n}\n\n.has-custom-scroll::-webkit-scrollbar-corner {\n  background-color: transparent;\n}\n\n.has-custom-scroll::-webkit-scrollbar-track {\n  background-color: #f5f5f5;\n}\n\n.has-custom-scroll::-webkit-scrollbar-thumb {\n  background-color: #d8d8d8;\n  -webkit-box-shadow: inset 1px 1px 0 rgba(0, 0, 0, 0.1), inset 0 -1px 0 rgba(0, 0, 0, 0.07);\n}\n\n// == Mail answer module styles\n.is-floated {\n  position: absolute;\n\n  &.y-center {\n    top: 50%;\n    transform: translateY(-50%);\n  }\n\n  &.right {\n    right: 0.938rem;\n\n    @include media('>=ipad') {\n      right: 1.563rem;\n    }\n  }\n}\n","/**\n* Header scss file which includes style rules related to header\n*/\n\n// == topnav bar :: header\n.topnavbar {\n  padding: 0.938rem 0.938rem; //~0.938rem; //~0.875rem 1.875rem;\n  background: transparent;\n  transition: all ease-in-out 0.35s;\n\n  &.has-menu-shown {\n    background: transparentize($brand-secondary, 0.1);\n  }\n\n  @include media('>sm') {\n    padding: 1.188rem 0.938rem; //1.25rem 0;//~0.938rem; //~0.875rem 1.875rem;\n  }\n\n  @include media('>=ipad') {\n    padding-top: 1.5rem;\n    padding-bottom: 1.5rem; //~1.875rem; //~0.875rem 1.875rem;\n\n    // == is stuck on page scroll\n    &.header-is-stuck {\n      padding-top: 1rem;\n      padding-bottom: 1rem;\n    }\n  }\n\n  @include media('>=laptop') {\n    padding-right: 1.875rem;\n    padding-left: 1.875rem;\n  }\n\n  // == secondary / inner page header\n  &-secondary {\n    background-image: linear-gradient(267deg, #34495e, #2c3e50);\n    box-shadow: 0 2px 4px 0 rgba($brand-light-gray, 0.38);\n  }\n\n  // == is stuck on page scroll\n  &.header-is-stuck {\n    background-image: linear-gradient(267deg, #34495e, #2c3e50);\n    box-shadow: 0 2px 4px 0 rgba($brand-light-gray, 0.38);\n  }\n}\n\n// == header columns\n.navbar-brand-col {\n  flex: 0 0 125px;\n  max-width: 125px;\n  @include media('>sm') {\n    flex: 0 0 15%;\n    max-width: 15%;\n  }\n}\n\n// == Navbar menu col\n.navbar-menu-col {\n  display: flex;\n  align-items: center;\n  justify-content: flex-end;\n  flex: 0 0 calc(100% - 125px);\n  max-width: calc(100% - 125px);\n  @include media('>sm') {\n    flex: 0 0 85%;\n    max-width: 85%;\n  }\n}\n\n// == header logo\n.navbar-brand {\n  display: flex;\n  align-items: center;\n  color: $brand-white !important;\n  font-size: 0.75rem;\n  letter-spacing: 1px;\n  line-height: 1;\n\n  @include media('>sm') {\n    font-size: 1.25rem;\n  }\n}\n\n.navbar-brand-logo {\n  margin-right: 5px;\n  max-width: 20px;\n  height: 32px;\n\n  @include media('>sm') {\n    margin-right: 10px;\n    max-width: 32px;\n  }\n}\n\n// == User actions\n.list-style-none {\n  list-style: none;\n}\n\n@include media('>sm') {\n  .user-actions-list {\n    margin-right: -0.75rem;\n    margin-left: -0.75rem;\n    padding: 0 0 0 2.5rem !important;\n  }\n\n  .user-actions-item {\n    padding-right: 0.75rem;\n    padding-left: 0.75rem;\n  }\n}\n\n.external-page-user-actions-list {\n  margin-right: -0.75rem;\n  margin-left: -0.75rem;\n}\n\n.user-actions-item.is-external-page {\n  @include media('<=sm') {\n    padding-right: 0.75rem;\n    padding-left: 0.75rem;\n  }\n}\n\n.user-actions-list {\n  display: flex;\n  align-items: center;\n\n  @include media('<ipad', '>sm') {\n    padding-left: 0.25rem !important;\n  }\n}\n\n.user-actions-item:not(.btn-dropdown):not(.is-external-page) {\n  @include media('>sm', '<ipad') {\n    position: absolute;\n    right: 21rem;\n\n    &:nth-of-type(2) {\n      right: 13.25rem;\n    }\n  }\n}\n","/**\n* @partial: _preloader.scss\n*/\n\n// == Preloader\n.app-preloader {\n  position: fixed;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  z-index: $zindex-top;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: $brand-blue;\n  transition: all ceaser($easeInOutQuint) 0.35s;\n\n  .page-loaded & {\n    opacity: 0;\n    pointer-events: none;\n    visibility: hidden;\n  }\n}\n\n// == Preloader content\n.app-preloader-content {\n  margin: 0 auto;\n  padding: 1.875rem 0.938rem;\n  max-width: 800px;\n\n  .h3 {\n    line-height: 1.21;\n  }\n}\n\n.app-preloader-logo {\n  margin-bottom: 1.875rem;\n\n  @include media('>=ipad') {\n    margin-bottom: 3rem;\n  }\n\n  .img {\n    height: 7.5rem;\n    width: 7.5rem;\n    animation: bounceY 3s infinite;\n\n    @include media('>=ipad') {\n      height: 13.5rem;\n      width: 13.5rem;\n    }\n  }\n}\n\n// == Animation keyframe for preloader logo\n@keyframes spreadOuterShadow {\n  0% {\n    opacity: 0;\n  }\n  30% {\n    opacity: 1;\n  }\n  60% {\n    box-shadow: 0 0 0 60px rgba(255, 255, 255, 0.1);\n    opacity: 0;\n  }\n  100% {\n    opacity: 0;\n  }\n}\n\n@keyframes bounceY {\n  0% {\n    transform: scale3d(1, 1, 1) translate(0, 0);\n  }\n  35% {\n    transform: scale3d(0.9, 0.9, 1) translate(-10px, 10px);\n  }\n  70% {\n    transform: scale3d(1, 1, 1) translate(0, 0);\n  }\n}\n","/**\n* Menu scss file which includes style rules related to menu\n*/\n\n// == Navigation bar\n\n.nav-link,\n.u