ft: 0;\n  height: 2px;\n  width: 1.125rem;\n  background: #fff;\n  border-radius: 1px;\n  opacity: 1;\n  transform: rotate(0deg);\n  transition: all ceaser($easeInOutQuint) 0.35s;\n\n  &:nth-child(2),\n  &:nth-child(3) {\n    top: 8px; //~14px;\n  }\n\n  &:nth-child(4) {\n    top: 13px; //~20px;\n  }\n}\n\n// == Collapsable menu\n@include media('<ipad') {\n  .navbar-collapsable-content {\n    position: fixed;\n    top: 70px;\n    right: 0;\n    left: 0;\n    opacity: 0;\n    pointer-events: none;\n    overflow-x: hidden;\n    overflow-y: auto;\n    letter-spacing: 0.9px;\n    visibility: hidden;\n    // transform: translateY(-1.2rem);\n    transition: all ceaser($easeInOutQuint) 0.35s;\n\n    .header-is-stuck & {\n      top: 64px;\n    }\n\n    &.shown {\n      opacity: 1;\n      pointer-events: auto;\n      visibility: visible;\n      transform: translateY(0rem);\n\n      .nav-item {\n        transform: translateY(0px);\n        animation: smoothAppearance 1s ease;\n        animation-fill-mode: forwards;\n\n        // Loop through the items and add some delay.\n        @for $i from 1 through 10 {\n          &:nth-child(#{$i}) {\n            animation-delay: (0.05s * $i);\n          }\n        }\n      }\n    }\n    ul {\n      padding: 1.875rem 0.938rem;\n      background: transparentize($brand-secondary, 0.1);\n    }\n  }\n\n  .navbar-nav .nav-item {\n    opacity: 0;\n    text-align: center;\n    transform: translateY(-100px);\n    transition: all cubic-bezier(0.18, 1, 0.21, 1) 0.9s;\n\n    &:not(:last-of-type) {\n      margin-bottom: 1.125rem;\n    }\n  }\n\n  .nav-link {\n    // padding-bottom: 0.15rem;\n  }\n\n  .btn.signup-btn {\n    padding: 0.563rem 0.5rem;\n    min-width: 100px;\n    font-size: 0.75rem;\n  }\n}\n\n@include media('<=sm') {\n  .navbar-collapsable-content {\n    top: 60px !important;\n  }\n}\n\n@include media('<ipad', '>sm') {\n  .navbar-nav .nav-item {\n    &:not(:last-of-type) {\n      margin-bottom: 1.75rem;\n    }\n  }\n\n  .navbar-nav .na