om: 3rem;\n  }\n}\n.l-mediakit-block-group .text-secondary {\n  font-weight: 300;\n  strong {\n    font-weight: 900;\n  }\n}\n.page-title {\n  margin-bottom: 1.875rem;\n  color: rgba(0, 0, 0, 0.87);\n\n  @include media('>sm') {\n    margin-bottom: 2.75rem;\n  }\n\n  @include media('>=ipad') {\n    margin-bottom: 3.75rem;\n  }\n  /* Blog Page\n\t=================================*/\n  @media (max-width: 767px) {\n    .page-title {\n      font-size: 1.5rem;\n    }\n    .featured-article {\n      margin-top: 1.5rem;\n    }\n  }\n}\n\n.l-block-subtitle {\n  line-height: 28px;\n}\n.about-title + .l-block-subtitle {\n  line-height: 36px;\n}\n\n.title-horz-line {\n  margin: 1.25rem 0;\n  padding: 1.125rem 0;\n  border-top: 3px solid #e6e8ea;\n  border-bottom: 3px solid #e6e8ea;\n  line-height: 1.6;\n}\n\n// == clipped shape\n.is-clipped {\n  position: relative;\n  margin-top: 3.875rem; //~4rem;\n\n  &-sm,\n  &.secure-email-block {\n    padding: 1.125rem 0 2.5rem;\n\n    @include media('>sm') {\n      padding: 1.125rem 0 3.75rem;\n      margin-top: 7.25rem; //~7rem;\n    }\n  }\n\n  &-lg {\n    margin-top: 3.125rem;\n    padding: 1.125rem 0 2.5rem;\n\n    @include media('<ipad') {\n      margin-top: 0;\n      padding-top: 0;\n    }\n  }\n\n  &.top::before,\n  &.bottom::after {\n    position: absolute;\n    top: -2rem;\n    content: '';\n    display: block;\n    background: $brand-faded;\n    width: calc(115% + 4rem);\n    height: 4.5rem;\n    transform: skew(48deg) rotate(-4deg);\n\n    @include media('>sm') {\n      height: 6rem;\n    }\n  }\n\n  &.top-left::before,\n  &.bottom-left::after {\n    // top: -4rem;\n    left: -9%;\n    transform: skew(48deg) rotate(-2deg);\n\n    @include media('<=sm') {\n      height: 3.45rem;\n      transform: skew(-48deg) rotate(-4deg);\n    }\n  }\n\n  &.top-right::before,\n  &.bottom-right::after {\n    right: -9%;\n    transform: skew(-48deg) rotate(2deg);\n\n    @include media('<=sm') {\n      height: 3rem;\n    }\n  }\n\n  &.bottom {\n 