ft: 1.875rem;\n  }\n}\n\n.px-md {\n  @include media('<ipad', '>sm') {\n    padding-right: 5.625rem;\n    padding-left: 5.625rem;\n  }\n}\n\n.px-lg {\n  @include media('<ipad', '>sm') {\n    padding-right: 8.438rem;\n    padding-left: 8.438rem;\n  }\n}\n\n// == Pages > Price page\n.l-block-header .btn-dropdown {\n  @include media('>sm') {\n    position: absolute;\n    top: 0.875rem;\n    right: 0;\n  }\n\n  @include media('<ipad', '>sm') {\n    top: -0.125rem;\n    right: 3rem;\n  }\n}\n\n// == Pages > donate page\n.donate-content-holder {\n  max-width: 585px;\n}\n\n// == Pages > media kit page\n.v-card {\n  position: relative;\n  overflow: hidden;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n\n  &::after {\n    content: '';\n    display: block;\n    padding-bottom: percentage(198 / 264);\n    width: 100%;\n  }\n}\n\n.media-card-content {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  padding: 0.938rem;\n  width: 100%;\n  transform: translateX(-50%) translateY(-50%);\n}\n\n.media-actions-list {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  margin-right: -0.75rem;\n  margin-left: -0.75rem;\n\n  > li {\n    padding-right: 0.75rem;\n    padding-left: 0.75rem;\n    line-height: 1;\n\n    > a {\n      color: $brand-success;\n      font-size: 0.875rem;\n\n      &:hover {\n        color: darken($brand-success, 5%);\n      }\n\n      i {\n        display: inline-block;\n        margin-right: 0.5rem;\n      }\n    }\n  }\n}\n\n// == Pages > onions page\n.onions-pic-holder {\n  max-width: 360px;\n}\n\n.btn-onion-lg {\n  padding: 0.8rem;\n  min-width: 250px;\n  font-size: 1rem;\n  border-radius: 2px;\n  color: #ffffff;\n  background-color: $brand-secondary;\n  border-color: $brand-secondary;\n\n  a {\n    color: white;\n  }\n\n  @include media('<=sm') {\n    padding-right: 0.938rem;\n    padding-left: 0.938rem;\n    font-size: 0.875rem;\n  }\n}\n\n// == Pages > privacy policy\n.static-text-holder {\n  h3 {\n    &:first-