n    }\n  }\n}\n\n.lists-dashed {\n  margin-right: -13px;\n  margin-left: -13px;\n\n  > li {\n    position: relative;\n    padding: 0 13px;\n\n    &:not(:last-of-type) {\n      &::before {\n        position: absolute;\n        top: 0%;\n        right: 0;\n        bottom: 0;\n        content: '';\n        border-right: 1px solid $menu-link;\n        width: 1px;\n        // transform: translateY(-50%);\n      }\n    }\n  }\n}\n\n// == Editor list\n.lists-editor {\n  margin: 1.5rem 0;\n}\n\n.lists-circle-arrow {\n  > li {\n    position: relative;\n    padding-left: 2.125rem;\n\n    &:not(:last-of-type) {\n      margin-bottom: 0.875rem;\n    }\n\n    &::before {\n      position: absolute;\n      top: 0.125rem;\n      bottom: 0;\n      left: 0;\n      content: '';\n      border: 1px solid #e0e0e0;\n      border-radius: 50%;\n      height: 1.375rem;\n      width: 1.375rem;\n    }\n\n    &::after {\n      position: absolute;\n      top: 0.6rem;\n      left: 0.45rem; //~0.5rem;\n      content: '';\n      border-style: solid;\n      border-width: 0.0938rem 0.0938rem 0 0;\n      width: 0.35rem;\n      height: 0.35rem;\n      transform: rotate(45deg);\n    }\n  }\n}\n","/**\n* Card modules scss file which includes reusable cards related style rules\n*/\n\n@import 'post';\n@import 'single-post';\n@import 'package';\n@import 'square';\n","/**\n* Post card modules scss file which includes reusable post cards\n*/\n\n// == badge\n.badge {\n  z-index: 1;\n  display: inline-block;\n  padding: 0.219rem 0.681rem;\n  vertical-align: top;\n}\n\n// == Feature card\n.feature-icon-holder {\n  margin-bottom: 1.25rem;\n\n  @include media('>sm') {\n    margin-bottom: 2.5rem;\n  }\n}\n\n// == News / Blog post cards\n.news-card-holder {\n  color: $brand-gray;\n  font-size: 0.875rem;\n\n  @include media('>sm') {\n    font-size: 0.75rem;\n  }\n\n  &:hover,\n  &:focus {\n    color: $brand-gray;\n  }\n\n  &:hover {\n    .news-img {\n      transform: scale3d(1.2, 1.2, 1.2);\n      transition: all ceaser($ease) 3s;\n    }\n  }\n}\n\n.news-card .badge {\n  position: absolute;\n  top: 0.75rem;\n  right: 0.75rem;\n  letter-spacing: 0.5px;\n}\n\n.news-pic-holder {\n  position: relative;\n  overflow: hidden;\n  margin-bottom: 1rem;\n  border-radius: 4px;\n\n  .news-img {\n    border-radius: 4px;\n    transform: scale3d(1, 1, 1);\n    transition: all ceaser($ease) 1.5s;\n    height: 202px;\n    width: 360px;\n  }\n}\n\n.news-card-title {\n  margin-bottom: 0.875rem;\n  color: rgba(0, 0, 0, 0.87);\n  font-weight: 500;\n}\n\n.news-card-footer {\n  margin-top: 0.75rem;\n  padding-top: 0.875rem;\n  border-top: 1px solid rgba(0, 0, 0, 0.12);\n\n  .news-card-full & {\n    @include media('>=laptop') {\n      margin-top: 3.25rem;\n    }\n  }\n}\n\n.news-meta-list {\n  display: flex;\n  align-items: center;\n  margin: 0 -10px;\n  padding-left: 0;\n  color: $brand-light-gray;\n  font-style: italic;\n  list-style: none;\n\n  > li {\n    flex: 0 0 50%;\n    max-width: 50%;\n    padding: 0 10px;\n    &:last-of-type {\n      text-align: right;\n    }\n  }\n}\n\n.news-card-holder p {\n  font-size: 14px;\n}\n.news-card-holder .post-meta {\n  &:after {\n    content: '';\n    display: block;\n    width: 0;\n    height: 0;\n    clear: both;\n  }\n  .post-date {\n    float: left;\n    font-style: italic;\n  }\n  .post-comments {\n    float: right;\n    font-style: italic;\n    border-bottom: 1px solid rgba(0, 0, 0, 0.38);\n  }\n}\n.l-block-footer .text-success {\n  margin-bottom: 30px;\n  display: inline-block;\n}\n@media (max-width: 767px) {\n  /* Home Page\n    =================================*/\n  .l-block-footer .offset-2 {\n    flex: 0 0 100%;\n    max-width: 100%;\n    margin-left: 0;\n  }\n  .l-block-footer .text-success {\n    margin-bottom: 0;\n  }\n}\n","/**\n* Single post card modules scss file which includes reusable single post articles\n*/\n\n// == Single post\n.single-post-card {\n  .news-meta {\n    margin-bottom: 1.25rem;\n    padding: 0.813rem 0;\n    border-bottom: 1px solid rgba(0, 0, 0, 0.12);\n    color: rgba(0, 0, 0, 0.38);\n    font-size: 0.875rem;\n\n    @include media('>=ipad') {\n      font-size: 0.75rem;\n    }\n  }\n\n  .news-meta-list {\n    > li {\n      position: relative;\n      flex-basis: auto;\n      max-width: inherit;\n      text-align: left !important;\n\n      &:not(:last-of-type) {\n        &::after {\n          position: absolute;\n          top: 50%;\n          right: 0;\n          content: '';\n          margin-top: 1px;\n          border-right: 1px solid;\n          height: 70%;\n          transform: translateY(-50%);\n        }\n      }\n\n      > a {\n        color: rgba(0, 0, 0, 0.38);\n      }\n    }\n  }\n  /* Single Blog Page\n    =================================*/\n  p + h2 {\n    font-size: 1rem;\n  }\n}\n\n// == Social sharer list\n.social-share-list {\n  display: flex;\n  margin: 0 -0.25rem;\n  padding: 0 0 0 0.5rem;\n\n  > li {\n    padding: 0 0.25rem;\n\n    > a {\n      &:hover {\n        .fab {\n          opacity: 0.85;\n        }\n      }\n    }\n  }\n\n  .fab {\n    font-size: 1.375rem;\n    vertical-align: top;\n    transition: all ceaser($easeInOutQuint) 0.35s;\n  }\n}\n\n.fa-facebook-square {\n  color: $facebook-color;\n}\n\n.fa-twitter-square {\n  color: $twitter-color;\n}\n/* Single Blog Page\n=================================*/\n.l-block-footer {\n  margin-top: 0 !important;\n}\n","/**\n* Package card modules scss file which includes style rules related to pricing\n*/\n\n// == package card\n.package-card {\n  padding-bottom: 1.875rem;\n  border-radius: 4px;\n\n  @include media('>sm') {\n    padding-bottom: 2.5rem; //~2.813rem;\n  }\n}\n\n.package-card-header {\n  position: relative;\n  padding: 0.813rem 1.25rem;\n  border-radius: 4px 4px 0 0;\n\n  @include media('>sm') {\n    padding: 1.813rem 1.25rem;\n  }\n\n  .js-card-swiper {\n    position: absolute;\n    top: 50%;\n    right: 1.25rem;\n    padding: 0 0;\n    background: none;\n    border: 0;\n    color: $brand-white;\n    line-height: 1;\n    outline: 0;\n    transform: translateY(-50%);\n\n    &.left {\n      right: auto;\n      left: 1.25rem;\n    }\n  }\n}\n\n.package-card-basic {\n  .package-card-header {\n    background: linear-gradient(259deg, #f1c20f, #f49315) !important;\n  }\n}\n\n.package-card-prime {\n  .package-card-header {\n    background: linear-gradient(259deg, #e74c3c, #c0392b) !important;\n  }\n}\n\n.package-card-champion {\n  .package-card-header {\n    background: linear-gradient(259deg, #2c4ac2, #3977e0) !important;\n  }\n}\n\n.package-card-body,\n.package-card-footer {\n  padding: 0 0.938rem;\n\n  @include media('>=ipad') {\n    padding: 0 1.25rem;\n  }\n}\n\n// == package card lists\n.package-list-item {\n  position: relative;\n  padding: 0.7rem 0.75rem 0.7rem 1.875rem;\n  line-height: 1;\n  font-weight: 400;\n\n  @include media('>=ipad') {\n    padding: 0.7rem 0.75rem 0.7rem 2.5rem;\n  }\n\n  &.has-border {\n    border-bottom: 1px solid $border-gray;\n\n    .fa {\n      position: absolute;\n    }\n\n    + .package-list-item:not(.has-border) {\n      margin-top: 0.75rem;\n    }\n  }\n\n  &.is-sm {\n    padding-top: 0.53rem;\n    padding-bottom: 0.53rem;\n  }\n\n  &.has-icon-center {\n    .fa {\n      top: 50%;\n      transform: translateY(-50%);\n    }\n  }\n\n  .fa {\n    position: absolute;\n    top: 50%;\n    left: 0;\n    margin-top: -8px;\n\n    @include media('>=ipad') {\n      left: 0.75rem;\n    }\n  }\n}\n\n// == package-text-holder\n.package-divider {\n  margin: 1.125rem 0 1.625rem;\n  padding: 0.938rem 0.75rem;\n  border-top: 1px solid $border-gray;\n  border-bottom: 1px solid $border-gray;\n\n  @include media('>sm') {\n    margin: 1.25rem 0 2.188rem;\n    padding: 2rem 0.75rem;\n  }\n}\n\n.package-divider-sm {\n  padding: 0.938rem 0 0.938rem 0; //~1.25rem 0.75rem;\n\n  @include media('>=ipad') {\n    padding: 0.938rem 0 0.938rem 0.75rem; //~1.25rem 0.75rem;\n  }\n}\n\n.package-storage-select {\n  padding: 0.15rem 0.625rem;\n  height: calc(2.876rem - 6px) !important;\n}\n\n.package-coming-soon {\n  h6 {\n    @include media('>sm') {\n      padding-left: 0.75rem;\n    }\n  }\n\n  .package-list {\n    color: rgba(97, 97, 97, 0.87);\n  }\n\n  .fa {\n    opacity: 0.4;\n  }\n}\n\n.pay-type-list {\n  > li {\n    &:not(:last-of-type) {\n      margin-bottom: 0.875rem;\n    }\n  }\n}\n\n.package-card .badge {\n  margin-left: 0.5rem;\n  padding: 0.5rem 0.3rem;\n  border: 1px solid $brand-primary;\n  color: $brand-primary;\n  font-size: 0.875rem;\n  font-weight: bold;\n  vertical-align: middle;\n}\n\n// == Small screen code for package cards\n@include media('<=sm') {\n  // .package-basic-col.is-inactive-slide,\n  // .package-prime-col.is-inactive-slide,\n  // .package-basic-col {\n  //     display: none;\n  // }\n\n  // .package-prime-col.is-inactive-slide + .package-basic-col {\n  //     display: block;\n  // }\n\n  .package-basic-col {\n    flex: 0 0 100%;\n    max-width: 100%;\n    transform: translateX(-45px);\n    transition: all ceaser($easeInOutQuint) 0.35s;\n\n    &.is-active-slide {\n      transform: translateX(-100%);\n    }\n\n    .package-card {\n      margin-right: 0;\n      margin-left: auto;\n    }\n  }\n\n  .package-prime-col {\n    flex: 0 0 100%;\n    max-width: 100%;\n    transform: translateX(calc(-100% + 45px));\n    transition: all ceaser($easeInOutQuint) 0.35s;\n\n    &.is-active-slide,\n    &.active-slide {\n      transform: translateX(0%);\n    }\n\n    .package-card {\n      margin-left: 0;\n    }\n  }\n\n  .package-card {\n    font-size: 0.875rem;\n    width: calc(100% - 15px);\n\n    h2 {\n      font-size: 1.75rem;\n    }\n\n    .btn-lg {\n      padding: 0.75rem 1.875rem;\n      min-width: 235px;\n    }\n  }\n\n  .currency-select-dropdown > .dropdown-toggle {\n    width: 100% !important;\n  }\n\n  .pay-price + .small {\n    font-size: 0.688rem;\n  }\n\n  // Package tab\n  .package-xs-tab {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    margin: 1.25rem -0.625rem 1.25rem;\n    padding: 0 0;\n    line-height: 1;\n\n    > li {\n      padding: 0 0.625rem;\n\n      &.is-active > a,\n      &.active > a {\n        color: $brand-black !important;\n        text-decoration: underline !important;\n      }\n    }\n\n    > li > a {\n      color: $brand-gray;\n      font-size: 0.875rem;\n      font-weight: 500;\n\n      &:hover {\n        color: $brand-black !important;\n      }\n    }\n  }\n}\n// == Setting page\n@include media('<sm') {\n  .package-card-holder {\n    overflow: auto;\n  }\n}\n","/**\n* Square card scss\n*/\n\n.square-card {\n  // position: relative;\n  // overflow: hidden;\n  // display: flex;\n  // align-items: center;\n  // justify-content: center;\n  text-align: center;\n\n  &::after {\n    display: block;\n    content: '';\n    padding-bottom: 100%;\n  }\n\n  &.has-border {\n    border: 1px solid $input-border-color;\n  }\n}\n\n.square-card-content {\n  position: absolute;\n  top: 2.813rem;\n  padding: 0 1.25rem;\n}\n\n.square-card-footer {\n  position: absolute;\n  bottom: 0.938rem;\n  padding: 0 1.25rem;\n  font-size: 0.875rem;\n}\n","/**\n* Comments modules scss file which includes style rules related to comment section\n*/\n\n.comment-row {\n  position: relative;\n  display: flex;\n  flex-wrap: wrap;\n  padding-bottom: 1.875rem;\n\n  &:not(:last-of-type) {\n    margin-bottom: 1.625rem;\n  }\n\n  @include media('>sm') {\n    padding-bottom: 0;\n  }\n}\n\n.comment-card {\n  &:not(:last-of-type) {\n    margin-bottom: 1.625rem;\n  }\n}\n\n.comment-avatar {\n  background: #e0e0e0;\n  border-radius: 4px;\n  height: 2.5rem;\n  width: 2.5rem;\n\n  @include media('>sm') {\n    height: 3.25rem;\n    width: 3.25rem;\n  }\n}\n\n.comment-detail {\n  flex: 0 0 calc(100% - 40px);\n  max-width: calc(100% - 40px);\n  padding-left: 0.938rem;\n\n  @include media('>sm') {\n    flex: 0 0 calc(100% - 52px);\n    max-width: calc(100% - 52px);\n  }\n}\n\n.comment-meta {\n  font-style: italic;\n  @include media('>sm') {\n    position: relative;\n  }\n\n  .comment-reply {\n    position: absolute;\n    bottom: 0;\n    font-style: normal;\n\n    @include media('>sm') {\n      top: 50%;\n      right: 0;\n      bottom: auto;\n      transform: translateY(-50%);\n    }\n\n    > a {\n      color: $brand-success;\n      font-size: 0.875rem;\n    }\n\n    .fas {\n      margin-right: 0.4rem;\n    }\n  }\n}\n\n.comment-meta-list {\n  margin: 0 0 10px;\n  padding: 0 0;\n  list-style: none;\n  font-size: 0.875rem;\n\n  @include media('>sm') {\n    display: flex;\n    align-items: center;\n  }\n\n  > li {\n    position: relative;\n    display: inline;\n    padding: 0 7px;\n\n    @include media('>sm') {\n      padding: 0 8px;\n    }\n\n    &:not(:last-of-type) {\n      &::after {\n        position: absolute;\n        top: 50%;\n        right: 0;\n        content: '';\n        margin-top: 1px;\n        border-right: 1px solid;\n        height: 60%;\n        transform: translateY(-50%);\n      }\n    }\n  }\n}\n\n.comment-body {\n  font-size: 0.875rem;\n  line-height: 1.67;\n\n  @include media('>sm') {\n    font-size: 0.75rem;\n  }\n}\n\n.comment-replies {\n  padding-left: 3.5rem;\n  @include media('>sm') {\n    padding-left: 5rem;\n  }\n}\n","/**\n* Bootstrap overrides scss file which includes other partials\n*/\n\n@import 'button';\n@import 'form';\n@import 'dropdown';\n@import 'modal';\n@import 'alert';\n@import 'tooltips';\n\n// == Text\n@include text-emphasis-variant('.text-dark', rgba(0, 0, 0, 0.87));\n@include text-emphasis-variant('.text-secondary', $brand-secondary);\n@include text-emphasis-variant('.text-secondary-faded', rgba(52, 73, 94, 0.87));\n@include text-emphasis-variant('.text-faded', #c4c4c4);\n@include text-emphasis-variant('.text-green', $brand-green);\n\n// == Background\n@include bg-variant('.bg-faded', $brand-faded);\n@include bg-variant('.bg-secondary', $brand-secondary);\n@include bg-variant('.bg-white', $brand-white);\n","/**\n* Bootstrap overrides button scss file\n*/\n\n.btn {\n  min-width: 100px;\n  text-transform: uppercase;\n\n  &:focus {\n    outline: none !important;\n    box-shadow: none !important;\n  }\n}\n\n.btn-lg {\n  min-width: 250px;\n  @include media('>sm') {\n    padding-top: 0.938rem;\n    padding-bottom: 0.938rem;\n  }\n}\n\n.btn-gradient-primary {\n  background: linear-gradient(259deg, #e74c3c, #c0392b) !important;\n  border: solid 1px #c0392b;\n  color: $brand-white !important;\n\n  &:hover {\n    background: linear-gradient(259deg, darken(#e74c3c, 5%), darken(#c0392b, 5%)) !important;\n  }\n}\n\n.btn-gradient-info {\n  background: linear-gradient(259deg, #f1c20f, #f49315) !important;\n  border-color: transparent;\n  color: $brand-white !important;\n\n  &:hover {\n    background: linear-gradient(259deg, darken(#f1c20f, 5%), darken(#f49315, 5%)) !important;\n  }\n}\n\n.btn-disabled {\n  @include button-variant($brand-light-gray, #f5f5f5, #e0e0e0);\n}\n\n// == gradients buttons\n// $btn_class_list: primary, secondary, success, info, warning, danger, dark;\n// $btn_color_val: $brand-primary, $brand-secondary, $brand-success, $brand-info, $brand-warning, $brand-danger, $brand-black;\n\n// @for $i from 1 through length($btn_class_list) {\n//     .btn-gradient-#{nth($btn_class_list, $i)} {\n//         background: linear-gradient(259deg, lighten(nth($btn_color_val, $i), 5%), darken(nth($btn_color_val, $i), 5%));\n//     }\n// }\n","/**\n* Bootstrap form override\n*/\n\nselect::-ms-expand {\n  display: none !important;\n}\n\n/* Fix chrome's autofill input filed issue */\ninput:-webkit-autofill {\n  -webkit-text-fill-color: $brand-black !important;\n  -webkit-box-shadow: 0 0 0 1000px $brand-white inset !important;\n}\n\n/* Include placeholder mixin */\n@include placeholder('.form-control', $brand-light-gray, '', $font-family-lato, 'initial', italic);\n@include placeholder('.form-control.error', $brand-danger, '', $font-family-lato, 'initial', italic);\n\n.form-group {\n  @include media('<=sm') {\n    margin-bottom: 1.25rem;\n  }\n}\n\n.form-group.form-group-sm {\n  margin-bottom: 1rem;\n}\n\n.form-control {\n  font-family: inherit;\n\n  @include media('<=sm') {\n    padding: 0.782rem 0.75rem;\n    font-size: 0.875rem;\n  }\n\n  &:focus {\n    //box-shadow: 0 0 4px 0 rgba(51, 72, 93, 0.3) !important;\n    border-color: $brand-secondary !important;\n  }\n\n  .is-validate & {\n    border-color: $brand-danger !important;\n  }\n}\n\n.input-group {\n  .form-control {\n    &:focus {\n      box-shadow: none !important;\n\n      + .input-group-addon {\n        border-color: $brand-secondary;\n      }\n    }\n  }\n}\n\n.form-control-lg {\n  @include media('<=sm') {\n    padding-top: 0.75rem;\n    padding-bottom: 0.75rem;\n  }\n}\n\n.input-group-addon {\n  transition: all ceaser($easeInOutQuint) 0.35s;\n}\n\n.validate-message {\n  color: $brand-danger;\n}\n\nselect,\nselect.form-control {\n  background: url(#{$images}select-arrow.svg);\n  background-position: calc(100% - 10px) center;\n  background-repeat: no-repeat;\n  background-size: 13px;\n\n  -webkit-appearance: none;\n  appearance: none;\n}\n\ntextarea.resize-off {\n  resize: none;\n}\n","/**\n* Bootstrap overrides dropdown scss file\n*/\n\n.dropdown[placement='bottom-right'] .dropdown-menu {\n  right: 0;\n  left: auto;\n}\n\n.dropdown-menu {\n  opacity: 0;\n  pointer-events: none;\n  z-index: 9999;\n  display: block !important;\n  // box-shadow: 0px 10px 40px 0px rgba(0, 0, 0, 0.1);\n  box-shadow: 0 6px 14px 0 rgba($brand-black, 0.17);\n  visibility: hidden;\n  transform: translateY(10px);\n  transition: all ceaser($easeInOutQuint) 0.35s;\n}\n\n.show > .dropdown-menu {\n  opacity: 1;\n  pointer-events: auto;\n  visibility: visible;\n  transform: translateY(0px);\n}\n\n.dropdown-menu-right {\n  left: auto !important;\n\n  button:disabled {\n    a {\n      color: grey !important;\n    }\n  }\n}\n\n.dropdown-toggle {\n  &::after,\n  &.hide-arrow::before {\n    display: none !important;\n  }\n\n  &::before {\n    position: absolute;\n    top: 50%;\n    right: 0